	import {uniCard, uniPagination} from '@dcloudio/uni-ui'	
	import FooterExplain from '@/pages/components/footerExplain'
	import findApi from '@/api/find.js'
	export default {
        components: {
            uniCard,
            uniPagination,
			FooterExplain
        },			
		data() {
			return {
				contentData: [],
				version: this.$configs.miniproConfings.version,
				shairePic_clickable: false,  // 控制邀请好友的disable状态
				// loading_show: false, // 控制页面loading的状态
				type: 1, // 1 总排名  2 点赞排名  3 邀请排名  
				bg: {
					'find_bg': `${this.$configs.baseImgsUrl+this.$configs.baseUrlConfigs.imgs_bg.find_bg}`
				},
				loadingMoreIsShow: false,
				authorzeIsShow: "",
				signData: {
					signDay: "0",
					rankNo: "0",
					totalDay: "0",
				},
				loadingMore: {}					
			};
		},
		onLoad () {
			this._getRankDayData()
		},
		methods:{
			// 获取list 列表数据
			_getRankDayData () {
				// debugger
				// uni.showLoading()
				let paramsObj = {
					params: {

					},
					page: {
						pageSize: 10,
						pageNum: 1
					}
				}
				findApi.getRankDayData( paramsObj, "loading", "加载中..").then(res => {
					// debugger
					// uni.hideLoading()
					console.log(res)
					if(res && res.data.code === 1){
						// debugger
						uni.showToast({
							title:"数据获取成功",
							icon: 'success',
							mask: true,
							duration:2000
						})
						this.contentData = res.data.data
						
						//页面跳转到
						// uni.redirectTo({
						// 	url:'../packageA/find/ranklist/index'
						// })
					}else{
						// uni.hideLoading()
						uni.showToast({
							title:"数据获取失败",
							icon: 'success',
							mask: true,	
							duration:2000
						})

					}
				}).catch(err => {
					// uni.hideLoading()
					uni.showToast({
						title:"数据获取失败,请重试",
						icon: 'success',
						mask:true,
						duration:2000
					})
				})
			}
		}
	}
